//import { playAudio } from "../util";
//import fontAwsome Icons
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faAngleLeft,
  faAngleRight,
  faPause,
} from "@fortawesome/free-solid-svg-icons";

const Player = ({
  songInfo,
  setSongInfo,
  audioRef,
  currentSong,
  isPlaying,
  setIsPlaying,
  songs,
  setCurrentSong,
  setSongs,
}) => {
  //useEffect
  const songSelected = (currentSong) => {
    const newSongs = songs.map(
      (song) => {
        if (song.id === currentSong.id) {
          return {
            ...song,
            active: true,
          };
        } else {
          return {
            ...song,
            active: false,
          };
        }
      }
      //[currentSong]
    );
    setSongs(newSongs);
  };
  //event handlers
  const playSongHandler = () => {
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(!isPlaying);
    } else {
      audioRef.current.play();
      setIsPlaying(!isPlaying);
    }
  };

  const skipHandler = async (dir) => {
    let currentSongIndex = songs.findIndex(
      (song) => song.id === currentSong.id
    );
    if (dir === `skip-forward`) {
      await setCurrentSong(songs[(currentSongIndex + 1) % songs.length]);
      songSelected(songs[(currentSongIndex + 1) % songs.length]);
    } else if (dir === `skip-back`) {
      if ((currentSongIndex - 1) % songs.length === -1) {
        await setCurrentSong(songs[songs.length - 1]);
        songSelected(songs[songs.length - 1]);
      } else {
        await setCurrentSong(songs[(currentSongIndex - 1) % songs.length]);
        songSelected(songs[(currentSongIndex - 1) % songs.length]);
      }
    }

    //playAudio(isPlaying, audioRef);
    if (isPlaying) {
      audioRef.current.play();
    }
    // console.log(currentSongIndex - 1);
  };
  //Add track style
  const trackAnimation = {
    transform: `translateX(${songInfo.animationPrograss}%)`,
  };

  const getTime = (time) => {
    //   console.log(songInfo);

    return (
      Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
    );
  };
  const dragInputHandler = (e) => {
    audioRef.current.currentTime = e.target.value;

    return setSongInfo({ ...songInfo, currentTime: e.target.value });
  };

  return (
    <div className="player">
      <div className="time-constrol">
        <p>{getTime(songInfo.currentTime)}</p>
        <div
          style={{
            background: `linear-gradient(to right, ${currentSong.color[0]}, ${currentSong.color[1]})`,
          }}
          className="track"
        >
          <input
            min={0}
            max={songInfo.duration || 0}
            value={songInfo.currentTime}
            onChange={dragInputHandler}
            type="range"
          />
          <div style={trackAnimation} className="animate-track"></div>
        </div>
        <p>{songInfo.duration ? getTime(songInfo.duration) : "0:00"}</p>
      </div>
      <div className="player-control">
        <FontAwesomeIcon
          onClick={() => skipHandler(`skip-back`)}
          className="skip-back"
          size="2x"
          icon={faAngleLeft}
        />

        <FontAwesomeIcon
          onClick={playSongHandler}
          className="play"
          size="2x"
          icon={isPlaying ? faPause : faPlay}
        />
        <FontAwesomeIcon
          onClick={() => skipHandler(`skip-forward`)}
          className="skip-forward"
          size="2x"
          icon={faAngleRight}
        />
      </div>
    </div>
  );
};

export default Player;
