//import { playAudio } from "../util";
import React from "react";
const LibrarySongs = ({
  song,
  songs,
  setCurrentSong,
  id,
  audioRef,
  isPlaying,
  setIsPlaying,
  setSongs,
}) => {
  const selectSongHandler = async () => {
    //  const selectedSong = songs.filter((state) => state.song.id === song.id);
    //check which song is selected

    await setCurrentSong(song);
    console.log(song);
    // console.log(songs.active);
    //add Active state
    const newSongs = songs.map((song) => {
      if (song.id === id) {
        return {
          ...song,
          active: true,
        };
      } else {
        return {
          ...song,
          active: false,
        };
      }
    });
    setSongs(newSongs);
    if (isPlaying) {
      audioRef.current.play();
    }
    //playAudio(isPlaying, audioRef);
    //check if the song is playing

    // audioRef.current.play();
  };
  return (
    <div
      onClick={selectSongHandler}
      className={`library-song ${song.active ? "selected" : ""}`}
    >
      <img alt="song-coverphoto" src={song.cover}></img>
      <div className="Song-Discrption">
        <h3>{song.name}</h3>
        <h4>{song.artist}</h4>
      </div>
    </div>
  );
};

export default LibrarySongs;
