import React from "react";
const SongInfo = ({ currentSong }) => {
  return (
    <div className="song-container">
      <img alt="song-coverphoto" src={currentSong.cover}></img>
      <h2>{currentSong.name}</h2>
      <h3>{currentSong.artist}</h3>
    </div>
  );
};

export default SongInfo;
